.club-member {
  padding: 84px 0 123px 0;

  @include vp-767 {
    padding: 35px 0 55px 0;
  }

  &__wrap {
    display: flex;
    justify-content: center;
    gap: 81px;

    @include vp-1023 {
      gap: 30px;
    }

    @include vp-767 {
      flex-direction: column;
      justify-content: flex-start;
      //align-items: center;


    }
  }

  &__item {
    display: flex;
    justify-content: center;
    flex-direction: column;

    width: 40.56%;
    padding: 62px 71px 60px 53px;
    border-radius: 21px;
    border: 2px solid rgba($color-default-white, 0.5);

    font-family: $ff--notosans;

    @include vp-1023 {
      padding: 37px 52px 38px 31px;
    }

    @include vp-767 {
      width: 100%;
      max-width: 345px;
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 30px;

    font-size: 30px;
    font-weight: 700;
    line-height: 41px;

    @include vp-1023 {
      font-size: 24px;
      line-height: 33px;
    }
  }

  &__link {
    font-size: 24px;
    font-weight: 400;
    line-height: 33px;
    text-decoration: underline;
    text-underline-offset: 2px;

    @include vp-1023 {
      font-size: 18px;
      line-height: 25px;
    }
  }
}
